export default {
  name: 'modal',

  props: {
    title: {
      type: String,
      required: true
    },
    onClose: {
      type: Function,
      required: true
    },
    onSubmit: {
      type: Function,
      required: false,
      default: () => {}
    },
    closeOnBackdrop: {
      type: Boolean,
      required: false,
      default: true
    },
    hasForm: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  methods: {
    close() {
      if (this.closeOnBackdrop) {
        this.onClose();
      }
    }
  },

  mounted() {
    document.body.insertBefore(this.$el, document.body.lastChild);
  }
}



