export default {
  name: 'wasabi-logo',

  props: {
    width: {
      type: Number,
      required: false,
      default: 16
    },
    height: {
      type: Number,
      required: false,
      default: 16
    },
    color: {
      type: String,
      required: false,
      default: '#000'
    }
  },

  computed: {
    styles() {
      return {
        fill: this.color,
        fillRule: 'nonzero'
      }
    }
  }
}



