import TreeNode from './TreeNode.vue';

export default {
  name: 'tree',

  props: {
    nodes: Array,
    level: {
      type: Number,
      required: false,
      default: 0
    },
    spanLevel: {
      type: Array,
      required: false,
      default: () => []
    },
    parentRefs: {
      type: Object,
      required: false,
      default: () => {}
    }
  },

  components: {
    TreeNode
  },

  methods: {
    isLast(index) {
      return (index + 1) === this.nodes.length;
    },

    spanLevels(level, isLast) {
      if (level === 0) {
        return [level];
      }

      if (isLast) {
        return this.spanLevel;
      }

      return this.spanLevel.concat([level]);
    },

    getNodeById(id) {
      return this.$store.getters['tree/getNodeById'](id);
    }
  }
}



