export default {
  name: 'loading-button',

  props: {
    loading: {
      type: Boolean,
      required: true
    },
    success: {
      type: Boolean,
      required: false,
      default: false
    },
    onClick: {
      type: Function,
      required: false,
      default: () => {}
    },
    disabled: {
      type: Boolean,
      required: false,
      default: false
    }
  },

  computed: {
    buttonClasses() {
      return {
        'loading-button': true,
        'loading-button__loading': this.loading,
        'loading-button__success': this.success && !this.loading
      };
    },

    spinnerClasses() {
      return {
        'loading-button--spinner': this.loading,
        'loading-button--check': this.success && !this.loading
      }
    },

    showSlot() {
      return this.loading || (!this.loading && this.success);
    }
  }
}



